





























import Vue from 'vue';
import MemberGroupApi from '@/apis/MemberGroupApi';
import { MemberGroupPreview } from '@/apis/models/MemberGroupModel';
import { WorkspaceDataModel } from '@/apis/models/WorkspaceModel';

const MODAL_WIDTH = 656;
export default Vue.extend({
  data() {
    return {
      modalWidth: MODAL_WIDTH,
      memberGroupName: '',
      remind: '',
    };
  },
  computed: {
    currentWorkspace(): WorkspaceDataModel {
      return this.$store.state.currentWorkspace;
    },
  },
  created() {
    this.modalWidth = window.innerWidth < MODAL_WIDTH ? MODAL_WIDTH / 2 : MODAL_WIDTH;
  },
  methods: {
    beforeOpen() {
      this.memberGroupName = '';
      this.remind = '';
    },
    cencelCreateMemberGroup() {
      console.log('取消創建班級');
      this.$modal.hide('create-memberGroup-modal');
    },
    createMemberGroup() {
      const regex = /^[^\s_!¡?÷?¿/\\+=@#$%ˆ&*(){}|~<>;:[\]]{2,}$/g;
      console.log(this.memberGroupName.search(regex));
      if (this.memberGroupName.search(regex) === 0) {
        this.remind = '';
        MemberGroupApi.create(this.currentWorkspace.uuid, this.memberGroupName).then((newMemberGroupObject: MemberGroupPreview) => {
          this.$modal.hide('create-memberGroup-modal');
          this.$store.state.theMemberGroups.push(newMemberGroupObject);
          console.log(newMemberGroupObject);
        });
      } else {
        this.remind = '不允許任何空白、特殊符號，且至少要兩個字以上';
      }
    },
  },
});
